@import "~@/assets/css/_variables.scss";

#bannerCarousel {
  .carousel-indicators [data-bs-target] {
    border: none;
    width: 16px;
    height: 4px;
    border-radius: 8px;
  }
  .carousel-item {
    position: relative;
    img {
      height: 120px;
      object-fit: cover;
      pointer-events: none;
      border-radius: 8px;
      user-select: none;
      filter: brightness(80%);
    }
    .title {
      position: absolute;
      border-radius: 0 0 8px 0;
      padding: 4px 8px 4px 56px;
      bottom: 0;
      right: 0;
      color: #fff;
      background: linear-gradient(
        to right,
        rgba($color: $c-main, $alpha: 0) 0%,
        rgba($color: $c-main, $alpha: 1) 48px
      );
      font-size: $fs * 1.5;
      font-weight: bold;
    }
  }
}
#market-nav-block {
  border-left: 1px solid #eee;
  border-right: 1px solid #eee;
  margin-right: 16px;
  min-height: 60vh;
  padding-top: 50px;
  li {
    &:last-child {
      position: relative;
      margin-top: 32px;
      &::after {
        position: absolute;
        content: "";
        height: 1px;
        width: 100%;
        background-color: #EEE;
        left: 0;
      }
      
    }
  a {
    border-bottom: 3px solid transparent;
    margin: 8px 32px 8px;
    padding-bottom: 4px;
    font-size: $fs * 1.5;
    font-weight: bold;
    letter-spacing: 4px;
    color: #555;
    transition: all 0.2s;
    &:hover {
      color: $c-main;
      border-bottom: 3px solid $c-main;
    }
    &.router-link-active {
      color: $c-main;
      border-bottom: 3px solid $c-main;
    }
  }
  }
}
